html, body, #root, .App {
  height: 100%;
  padding: 0px;
  margin: 0px;
}

.wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.site-description {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.site-description img {
  margin-top: 10px;
}

.site-description button {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100px;
  height: 30px;
  line-height: 25px;
}

.adoption-info-text {
  margin: 10px;
  white-space: pre-wrap;
  text-align: center;
  font-size: large;
  width: 80%;
}

h1 {
  margin: 0px;
  min-height: 100px;
  width: 100%;
  text-align: center;
  background-color: lightblue;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}